/* OgÃ³lne */
.ramka {
    width: 85%;
}

* {
    box-sizing: border-box;
    background-color: #eeeeee;
    font-family: 'Gill Sans', 'Arial', sans-serif;
    color: #333333;
    line-height: 2em;
}

html, body {
    margin: 0;
}

p {
    text-align: justify;
    width: 80%;
    margin-left: 10%;
}

p skladniki_okregu {
    line-height: 1em;
    font-size: 0.5em;
}

header {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    background-color: #333;
    color: #e3e3e3;
    text-align: center;
    padding: 1em;
}

/* Style the links */
header a {
    color: #eeeeee;
    background: none;
    text-decoration: none;
}

header a:hover {
    filter: brightness(5.0) drop-shadow(0 0 1em);
    margin: 0 10px;
}

#panel {
    display: flex;
    flex-direction: row;
}

.map {
    width: 100%;
    height: 100%;
}

svg.map {
    fill: #dddddd;
    stroke: #b0b0b0;
}

svg.map a:visited {
    fill: #dddddd;
}

svg.map a :hover {
    stroke-width: 5px;
    stroke-linejoin: round;
    stroke: black;
}

.mapaKontener {
    width: 60%;
}

.infoPoPrawej {
    width: 40%;
}

.info {
    display: none;
}

/* Wykresy */
.kontener {
    display: flex;
    /* Horizontal layout by default */
    flex-direction: row;
    /* Allow items to wrap to the next line on smaller screens */
}

.wykres {
    width: 50%;
    box-sizing: border-box;
}

.histogram {
    width: 33%;
    box-sizing:  border-box;
}

@media (max-width: 768px) {
    .ramka {
        width: 100%;
    }
    
    .kontener {
        flex-direction: column;
        /* Vertical layout for smaller screens */
    }

    .histogram {
        width: 100%;
        /* Full width for child elements on smaller screens */
        margin: 0;
        /* Reset margin for vertical layout */
    }

    .wykres {
        width: 100%;
        /* Full width for child elements on smaller screens */
        margin: 0;
        /* Reset margin for vertical layout */
    }

    .map {
        width: 100%;
        height: 100%;
    }

    #Info {
        display: none;
    }

    #panel {
        flex-direction: column;
    }

    .placeholder {
        display: none;
    }

    .mapaKontener {
        width: 100%;
    }
}


/* Podstrona Mapa */
.grid-container {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    /* You can adjust the number of columns as needed */
    gap: 10px;
    /* Adjust the gap between elements */
}

.grid-item {
    /* border: 1px solid #ccc; */
    padding: 5px;
    text-align: center;
}